@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
body {
    margin: 0;
    font-family: 'Indie Flower', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;
    height: 100%;
  }
  
  code {
    font-family: 'Indie Flower', cursive;
  }
  
nav {
    border: 2px solid black;
    background-image: linear-gradient(to right, darkred, red);
    font-family: 'Press Start 2P', cursive;
    font-weight: bold;
    color: white;
}

.navbar-brand {
    font-size: 26px;
}

.nav-link {
    font-size: 22px;
}
h1 {
    font-family: 'Press Start 2P', cursive;
}

img {
    padding: 20px;
}

.jumbotron {
    margin-top: 20px;
    margin-bottom: 20px;
    background-image: linear-gradient(to right, darkred, red);
    color: white;
}

.about {
    border: solid black 2px;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 10px;
    box-shadow: 2px 2px 2px black;
}

.contact {
    color: rgb(166, 181, 196);
    text-shadow: 2px 2px 2px black;
}

.bit {
    font-family: 'Press Start 2P', cursive;
}

@media (max-width: 760px) {
    .display-4 {
        display: none;
    }

    .hide-small {
        visibility: visible;
    }
}
.jumbotron {
    box-shadow: 5px 5px 5px black;
}

.web-thumb {
    width: 100%;
    height: 100%;
    border: solid black 2px;
    padding: 0;
    box-shadow: 2px 2px 2px black;
}

.button-div {
    align-content: center;
}

.port-button {
    align-content: center;
    background-image: linear-gradient(to right, darkred, red);
    color: white;
    border: solid black 2px;
    padding: 10px;
    box-shadow: 2px 2px 2px black;
    font-family: 'Press Start 2P', cursive;
    font-size: small;
}

button:hover {
    border: solid white 2px;
}

.port-card {
    border: solid 2px black;
    padding: 10px;
    box-shadow: 2px 2px 2px black;
}

.port-head {
    color: white;
    text-shadow: 2px 2px 2px black;
    text-align: center;
    margin: 10px;
    font-family: 'Press Start 2P', cursive;
}

.port-sub {
    color: white;
    text-shadow: 2px 2px 2px black;
    text-align: center;
    margin: 10px;
    font-family: 'Press Start 2P', cursive;
}

.port-desc {
    text-align: center;
    margin: 10px;
}

.port-tools {
    font-family: 'Press Start 2P', cursive;
    text-align: center;
}
.hide-small {
    visibility: hidden;
    font-family: 'Press Start 2P', cursive;
}

@media (max-width: 760px) {
    .display-4 {
        display: none;
    }

    .hide-small {
        visibility: visible;
    }
}
footer {
    background-image: linear-gradient(to right, darkred, red);
    color: white;
    font-family: 'Press Start 2P', cursive;
    font-weight: bold;
    text-align: center;
    width: 100%;
    padding: 10px;
    bottom: 0;
    left: 0;
}

#sticky-footer {
    flex-shrink: none;
  }
.wrapper {
    margin: 0;
    font-family: 'Indie Flower', cursive;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;
    height: 100%;
  }

#wrapper {
  background-image: url(../../static/media/cityscape.48830a53.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
}
  
  code {
    font-family: 'Indie Flower', cursive;
  }
  
